*{ margin: 0; padding: 0; box-sizing: border-box;}

.container-fluid{
    width: 90%!important;
    margin: auto;
}
table{
    position:relative;
    
}
thead{
    position: sticky;
    top: 0;
}
thead::after{
    content: '';
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
}

.table-responsive{
    max-height: 700px;
    box-shadow:  0 25px 50px -12px  rgba(0,0,0,0.25);
}

tr:hover{
    background-color: #ffa;
}

.table td,
.table th{
    padding: 0.75rem;
    vertical-align: top;
    border-top: none;
}
td:hover ::after,
th:hover ::after{
    content: '';
    position: absolute;
    left: 0;
    top: -5000px;
    height: 5000px;
    width: 100%;
    z-index: -1;
}
.table tr td:hover:nth-child(5){
    background-color: rgb(231, 83,115);
    color:aliceblue;
    border: 0.5px solid rgb(12, 12, 12);
}
.table tr td:hover {
    background-color: rgb(117, 223,122);
    color:aliceblue;
    border: 0.5px solid rgb(12, 12, 12);
}
.thead-dark{
    font-weight: bold;
    text-transform: uppercase;
    background-color: black;
    color: white;
}
.thead-dark tr:hover{
    background: black;
}
